@use 'base/variables' as v;
@use 'base/mixins' as m;

@include m.tablet {
    .iconos-nosotros {
        @include m.grid(3, 2rem);
    }
}

.icono {
  img {
    height: 7rem;
    width: auto;
    margin-left: auto;
    margin-right: auto;
    @include m.tablet {
      height: 10rem;
    }
  }
  h3 {
    text-transform: uppercase;
  }
  p {
    text-align: center;
    padding: 0 2rem;
    @include m.tablet {
      padding: 0 0;
    }
  }
}
