@use "base/variables" as v;
@use "base/mixins" as m;

.testimonial {
  background-color: v.$verde;
  padding: 2rem;
  color: v.$blanco;
  border-radius: 2rem;
  font-size: 1.8rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  @include m.tablet {
    font-size: 2.4rem;
  }

  blockquote {
    padding-left: 2rem;
    position: relative;
    @include m.tablet {
        padding-left: 5rem;
    }

    &::before {
      content: "";
      background-image: url("../img/comilla.svg");
      background-repeat: no-repeat;
      width: 6rem;
      display: block;
      position: absolute;
      height: 3rem;
      left: -3rem;
      @include m.tablet {
        height: 8rem;
        left: -2.5rem;
      }
    }
  }

  p {
    text-align: right;
    color: v.$blanco;
    margin-right: 2.5rem;
  }
}
